import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
export default {
	props: ['cartware', 'config'],
	data() {
		return {
			token: uni.getStorageSync('cdj_token'),
			imgRemote: imgRemote,
			kind: 0,
			value: 1,
			isTop: false,
			arrObj: {}
		}
	},
	methods: {
		selectSpec(e) {
			this.kind = e;
		},
		onClose() {
			this.$emit('onClose')
		},
		showKey() {
			this.isTop = true;
			this.arrObj = this.cartware;
			console.log(this.arrObj)
		},
		plus() {
			this.value++;
		},
		minus() {
			this.value--;
			if (this.value <= 0) {
				this.value = 1;
			}
		},
		toParent(e) {	
			console.log(this.cartware);
			// return;
			if(this.cartware.attr.length){
				if (this.cartware.attr[this.kind].is_float == 1 && !Number.isInteger(Number(e.val))) {
					rs.Toast( '购买数量不能为小数');
					return;
				}
			}
			
			this.isTop = false;
			this.value = e.val;
		},
		determine() {
			let info = this.cartware;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			
			let newobj = {};
			let itemId, attrId;
			if (info.attr.length == 0) {
				itemId = info.id;
				attrId = 0;
			} else {
				itemId = info.attr[this.kind].item_id;
				attrId = info.attr[this.kind].id
			}
			newobj = Object.assign({
					item_id: itemId,
					attr_id: attrId,
					item_num: this.value
				},
				obj
			);
			let sign = md5.hexMD5(rs.objKeySort(newobj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				newobj
			);

			rs.postRequests('firstChangeNum', params, res => {
				if (res.data.code == 200) {
					rs.Toast('成功加入购物车');
					let pages = getCurrentPages();

					if (pages[0].route == 'pages/tabar/shopcart') {

						uni.reLaunch({
							url: '/pages/tabar/shopcart'
						})





					}
				} else if (res.data.code == 407 || res.data.code == 406) {
					rs.Toast('购买数量不能超过活动数量')
				} else {
					rs.Toast(res.data.msg)
				}
			});
			this.onClose();
		}
	}
};
